import React from 'react';
import { useState } from "react"
import { useNavigate } from "react-router-dom"
import axios from 'axios'



function SignUpComponent(){

    const [username, setUsername] = useState()
    const [password, setPassword] = useState()
    const [erroMsg, setErrorMsg ] = useState()

    const navigate = useNavigate()

    function handleUsernameChange(event){
        setUsername(event.target.value)
    }

    function handlePasswordChange(event){
        setPassword(event.target.value)
    }

    //todo send post request to springboot and navigate to login
    // function handleSubmit(event){
    //     console.log(username);
    //     console.log(password);
    //     navigate(`/login`)
    // }

    function handleSubmit() {
        axios.post('http://vcm-30567.vm.duke.edu:8080/api/users', {
            username: username,
            password: password,
            state : "active"
        })
        .then(function (response) {
          // handle success, for example navigate to login page

            navigate(`/login`)
            
            
        })
        .catch(function (error) {
          // handle error
            if(error.response.status === 409){
                setErrorMsg("User Account already exist, try a new one.")
            }
            console.log(error);
        });
    }
    


    return (
        <div className="SignUp">
            <h1>Sign Up: </h1>
            {erroMsg && <h1>{erroMsg}</h1>}
            <div className="SingUpForm">
                <div>
                    <label>User Name: </label>
                    <input type="text" name="username" value={username} onChange={handleUsernameChange}/>
                </div>
                <div>
                    <label>Password: </label>
                    <input type="password" name="password" onChange={handlePasswordChange}/>
                </div>
                <div>
                    <button type="button" name="login" onClick={handleSubmit}>Sign Up</button>
                </div>
            </div>
        </div>
    )
}

export default SignUpComponent